import VueRouter from "vue-router"
import Vue from "vue";

Vue.use(VueRouter);
import AllSite from "@/views/AllSite"
import OverView from "@/views/AllSite/OverView"
const routes = [
    { path: '/',redirect:"/AllSite/OverView"},
    { path: '/AllSite',redirect:"/AllSite/OverView"},
    {
        path: '/AllSite/OverView',
        component:AllSite,
        children: [
            {
                path: '',
                component:OverView,
            }
        ]
    },
]

const router = new VueRouter({
    routes, // `routes: routes` 的缩写
    //滚动行为
  scrollBehavior() {
    //返回的这个y=0，代表的滚动条在最上方
    return { y: 0 };
  },
});

export default router