<template>
  <el-col :span="3">
      <el-menu
      default-active="0"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose">
      <el-menu-item index="0">
        <i class="el-icon-s-home"></i>
        <span slot="title">网站概况</span>
      </el-menu-item>
        <!-- index1 item1 表示第一级 -->
        <el-submenu :index="(index1+1).toString()" v-for="(item1,index1) in menu" :key="index1">
            <template slot="title">
            <i :class="whichIcon(index1)"></i>
            <span>{{item1.title}}</span>
            </template>
            <el-menu-item-group >
              <!-- 第二级 -->
              <el-menu-item :index="(index2+1).toString()" v-for="(item2, index2) in item1.items" :key="index2">{{item2}}</el-menu-item>
            </el-menu-item-group>
        </el-submenu>
      </el-menu>
  </el-col>
</template>

<script>
export default {
    name:"",
    data(){
      return {
        title:"网站概况",
        menu:[
          {
            title:"流量分析",
            items:[
              "趋势分析",
              "对比分析",
              "当前在线",
              "访问明细",
            ]
          },
          {
            title:"来源分析",
            items:[
              "来源分类",
              "搜索引擎",
              "搜索词",
              "最近搜索",
              "SEO推荐",
              "来路域名",
              "来路页面",
              "来源升降榜",
            ]
          },
        ]
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      whichIcon(index){
        if(index === 0){
          return "el-icon-s-marketing"
        }
        else if(index === 1){
          return "el-icon-map-location"
        }
      }
    }
}
</script>

<style scoped lang="less">
  .el-submenu .el-menu-item {
      height: 50px;
      line-height: 50px;
      padding: 0px;
      min-width: 138px;
  }
</style>